/* General Styles */
body {
    margin: 0;
    font-family: Arial, sans-serif;
  }
  
  .ul{
    list-style: none;
    list-style-type: none ;
  }
  .navbar {
    margin-top: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    color: #164659;
    list-style: none;
    
  }
  
  .logo {
    margin-top: 1rem;
    font-size: 2.5rem;
    font-weight: bold;
    margin-left: 2rem;
    width: 4%;
    
  }
  
  .nav-links {
    display: flex;
    list-style: none;
    font-family: "Calibri";
    color: #164659;
    font-weight: 700;
    font-size: 20px;
    list-style: none;
    gap: 1.2em;
    --gap: clamp(1.5rem, 5vw, 3.5rem);
    --underline-gap: 2rem;
    margin-right: 40rem;
    margin-left: 45rem;
  }
  
  .nav-links li {
    margin: 0 15px;
  }
  
  .nav-links a {
    text-decoration: none;
    color: #164659;
    transition: color 0.3s;
    list-style: none;
    
  }
  
  .nav-links a:hover {
    color: #164659;
  }
  
  /* Mobile Menu */
  .mobile-menu-icon {
    display: none;
    font-size: 1.8rem;
    color: #164659;
    background: none;
    border: none;
    cursor: pointer;
  }
  
  

  .header{
    display: flex;
    justify-content: space-between;
    align-items:center;
    /* margin: 2em; */
    margin-top: 2.5rem;
    margin-bottom: 3rem;
  
  }


 

  /* Responsive Design */
  @media screen and (max-width: 768px) {
    .nav-links {
      display: none;
      list-style: none;
    }
    .logo{
        width: 13%;
    }
    .routes{
        list-style: none;
        list-style-type: none;
    }
    
    .nav-links-mobile {
      text-decoration: none;
      display: flex;
      flex-direction: column;
      position: absolute;
      top: 2px;
      right: 0;
      background-color: white;
      width: 80%;
      height: 100%;
      text-align: center;
      color: #fff;
      list-style: none;
      padding: 0;
      list-style-type: none;
      text-decoration: none;
      background-color: #031D2B;
      opacity: 75%;
    
     
    }
    /* .hover-underline-animation{
      transform: none;
      transition: none;
    } */
   
    .nav-links-mobile li {
      margin: 10px 0;
    }
  
    .mobile-menu-icon {
      display:block;
    
    }
    .routes{
        list-style: none;
    }
    a{
        list-style: none;
    }
    .aclass{
        margin-top: 50rem;
        size: 2.3rem;
        font-size: 1.3rem;
        color: white;
    }
    .hover-underline-animation {
        display: inline-block;
        position: relative;
        color: white;
      }
      .hover-underline-animation:after {
        content: '';
        position: absolute;
        width: 100%;
        transform: scaleX(0);
        height: 2px;
        bottom: 0;
        left: 0;
        background-color: #164659;
        color:orange;
        
        transform-origin: bottom right;
        transition: transform 0.25s ease-out;
      }
      
      .hover-underline-animation:hover:after {
         transform: scaleX(1);
        transform-origin: bottom left;
        
      }
  
  }

  @media screen and (min-width:768px) {
    .hover-underline-animation {
        display: inline-block;
        position: relative;
        color: #164659;
      }


      
      .hover-underline-animation:after {
        content: '';
        position: absolute;
        width: 100%;
        transform: scaleX(0);
        height: 2px;
        bottom: 0;
        left: 0;
        background-color: #164659;
        color:orange;
        
        transform-origin: bottom right;
        transition: transform 0.25s ease-out;
      }
      
      .hover-underline-animation:hover:after {
         transform: scaleX(1);
        transform-origin: bottom left;
        
      }
  }
  
 